<template>
    <div id="app">
        <el-card class="box-card" id="volTitle">
            <div style="font-size: 30px"></div>
        </el-card>
        <div id="volunteer">
            <el-form :model="volunteerForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
                <el-form-item label="" prop="name">
                    <el-input v-model="volunteerForm.name"></el-input>
                </el-form-item>

                <el-form-item label="" prop="phoneNumber">
                    <el-input v-model="volunteerForm.phoneNumber"></el-input>
                </el-form-item>

                <el-form-item label="QQ" prop="qq">
                    <el-input v-model="volunteerForm.qq"></el-input>
                </el-form-item>

                <el-form-item label="" required>
                    <el-col :span="11">
                        <el-form-item prop="date1">
                            <el-time-picker placeholder="" v-model="volunteerForm.date1"
                                            style="width: 100%;"></el-time-picker>
                        </el-form-item>
                    </el-col>
                    <el-col class="line" :span="2">-</el-col>
                    <el-col :span="11">
                        <el-form-item prop="date2">
                            <el-time-picker placeholder="" v-model="volunteerForm.date2"
                                            style="width: 100%;"></el-time-picker>
                        </el-form-item>
                    </el-col>
                </el-form-item>

                <el-form-item label="" prop="isDoctor">
                    <el-switch v-model="volunteerForm.isDoctor"></el-switch>
                </el-form-item>

                <el-form-item label="" prop="advantage">
                    <el-checkbox-group v-model="volunteerForm.advantage">
                        <el-checkbox label="" name="type"></el-checkbox>
                        <el-checkbox label="" name="type"></el-checkbox>
                        <el-checkbox label="" name="type"></el-checkbox>
                        <el-checkbox label="" name="type"></el-checkbox>
                    </el-checkbox-group>
                </el-form-item>

                <el-form-item label="" prop="words">
                    <el-input type="textarea" v-model="volunteerForm.words" :rows="5"
                              placeholder=",100"></el-input>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" @click="submitForm('ruleForm')"></el-button>
                    <el-button @click="resetForm('ruleForm')"></el-button>
                </el-form-item>
            </el-form>
        </div>
    </div>
</template>

<script>
    export default {
        data() {
            return {
                volunteerForm: {
                    name: '',
                    phoneNumber: '',
                    qq: '',
                    date1: '',
                    date2: '',
                    isDoctor: false,
                    advantage: [],
                    words: ''
                },
                rules: {
                    name: [
                        {required: true, message: '', trigger: 'blur'},
                    ],
                    phoneNumber: [
                        {required: true, message: '', trigger: 'blur'},
                        {min: 11, max: 11, message: '11', trigger: 'blur'}
                    ],
                    qq: [
                        {required: true, message: 'QQ', trigger: 'blur'},
                        {min: 4, max: 10, message: '411', trigger: 'blur'}
                    ],
                    date1: [
                        {type: 'date', required: true, message: '', trigger: 'change'}
                    ],
                    date2: [
                        {type: 'date', required: true, message: '', trigger: 'change'}
                    ],
                    type: [
                        {type: 'array', required: true, message: '', trigger: 'change'}
                    ],
                    desc: [
                        {required: true, message: '', trigger: 'blur'},
                        {max: 100, message: '100', trigger: 'blur'}
                    ]
                }
            };
        },
        methods: {
            submitForm(formName) {
                let that = this;
                this.$refs[formName].validate((valid) => {
                    if (valid) {
                        let formData = new FormData();
                        for (let key in this.volunteerForm) {
                            formData.append(key, this.volunteerForm[key]);
                        }
                        this.axios({
                            method: 'post',
                            url: '/toBeVolunteer',
                            data: formData,
                        })
                            .then(function (response) {
                                    let msg = response.data.msg;
                                    if (msg === '') {
                                        that.$message({
                                            message: '!',
                                            type: 'success'
                                        });
                                    } else {
                                        that.$message({
                                            message: '',
                                            type: 'error'
                                        });
                                    }
                                },
                                function (err) {
                                    console.log(err);
                                }
                            );
                    } else {
                        return false;
                    }
                });
            },

            resetForm(formName) {
                this.$refs[formName].resetFields();
            },
        }
    }
</script>

