        border-radius: 9px;
        left: 50%;
    }

    #app {
        position: relative;
        width: 100%;
        height: 80%;
    }

    #volTitle {
        position: relative;
        width: 100%;
        left: 50%;
        transform: translate(-50%, -50%);
